<template>
  <div class="dz-header" :class="isListOpen? 'open':'close'">
    <!--前景层-->
    <div class="dz-header-front">
      <div class="logo-content">
        <router-link to="/">
          <div class="logo"><img src="@/assets/logo.svg" alt=""></div>
        </router-link>
      </div>
      <div class="humberger"><span></span><span></span><span></span></div>
      <!--分类筛选器-->
      <div class="list">
        <div class="tab">
          <div class="tab-underline" :class="listType"></div>
          <div class="tab-item" :class="[listType == 'work'? 'active':'']" @click="listType='work',resetScroll()">项 目</div>
          <router-link to="/resume"><div class="tab-item">简 历</div></router-link>
        </div>
        <!--导航部分-->
        <div class="scroll-bar" ref="scrollBar"></div>
        <div class="scroll-content-in-tab">
          <div class="scroll-content-in-tab-inner" ref="scrollInner">
            <!--卡片部分-->
            <transition-group name="fade-item">
              <router-link :to="item.link" v-for="item in items" :key="item.title" class="item-wrp">
                <div class="item">
                  <div class="img-content" v-if="item.img">
                    <img :src="item.img" alt="">
                  </div>
                  <h3 class="title">{{item.title}}</h3>
                  <p class="sub-title">{{item.subTitle}}</p>
                </div>
              </router-link>
            </transition-group>
          </div>
        </div>
      </div>
    </div>
    <!--背景层-->
    <div class="dz-header-bg"></div>
  </div>
</template>
<script>
import pageData from '@/page/index.js'
export default {
  data() {
    return {
      listType: 'work',
      isListOpen: true,
      scroll: {
        top: 0,
        height: 0,
        scrollHeight: 0
      }
    }
  },
  computed :{
    items() {
      return pageData[this.listType]
    }
  },
  mounted(){
    this.$refs.scrollInner.addEventListener('scroll', this.handleScroll) 
    const top = this.$refs.scrollInner.scrollTop
    const height = this.$refs.scrollInner.clientHeight
    const scrollHeight = this.$refs.scrollInner.scrollHeight
    const off = top / (scrollHeight-height) * (height-132)
    this.$refs.scrollBar.style.transform = 'translateY('+ off + 'px)'
  },
  methods: {
    handleScroll(e){
      const top = e.target.scrollTop
      const height = e.target.clientHeight
      const scrollHeight = e.target.scrollHeight
      const off = top / (scrollHeight-height) * (height-114)
      this.$refs.scrollBar.style.transform = 'translateY('+ off + 'px)'
    },
    resetScroll(){
      this.$refs.scrollInner.scrollTop = 0
      this.$refs.scrollBar.style.transform = 'translateY(0px)'
    }
  }
}
</script>
