<template>
  <div class="scroll-fade-component" ref = 'component' 
    :style="scrollAni(.05)">
    <slot></slot>
  </div>
</template>
<script>
export default {
    data() {
      return {
        toTop: 100000,
        height: 0
      }
    },
    mounted() {
      this.height = document.documentElement.clientHeight
      this.toTop = this.$refs.component.getBoundingClientRect().top - this.height 
    },
    methods: {
      scrollAni(start) {
        const scroll = this.$store.state.scroll
        const dur = this.height * .2
        const begin = this.toTop + this.height * start
        const over = this.toTop + this.height * start + dur
        let factor = 0 
        if (scroll < begin) {
          factor = 0
        } else if (scroll > over) {
          factor = 1
        } else {
          factor = 1 - (over - scroll)/dur
        }
        const style = 'transform:scale(' + (0.8 + 0.2*factor) +');' +
                      'opacity:' + factor + ';'
        return style
      }
    }
}
</script>