import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import CloudUI3 from '../page/work/CloudUI3.vue'
import CloudUI2 from '../page/work/CloudUI2.vue'
import Solution from '../page/work/Solution.vue'
import CxdDesign from '../page/work/CxdDesign.vue'
import Resume from '../views/Resume.vue'

Vue.use(VueRouter)
const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/resume',
    name: 'resume',
    component: Resume
  },
  {
    path: '/work/cloud-ui-3',
    name: 'CloudUI3',
    component: CloudUI3
  },
  {
    path: '/work/solution',
    name: 'Solution',
    component: Solution
  },
  {
    path: '/work/cloud-ui-2',
    name: 'CLOUD2',
    component: CloudUI2
  },
  {
    path: '/work/cxd',
    name: 'cxd',
    component: CxdDesign
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
