<script>
import pageData from '@/page/index.js'
export default {
  data(){
    return {
      activeData : 1
    }
  },
  computed :{
    items() {
      return pageData.work
    }
  },
  methods: {
    scrollAni(e){
      const scroll = this.$store.state.scroll
      let style = 'transform:translateY(0px);'
      if (scroll < 1200){
        style = 'transform:translateY(' + e * this.$store.state.scroll +'px);'       
      } else {
        style = 'transform:translateY(0px);'
      }
      return style
    },
    isActive(i){
      if (this.activeData == i)
       {
         return 'active'
       } else {
         return 'not-active'
       }
    },
    firstScroll(){
      if(this.$store.state.scroll < 100){
        return 'in-first-screen'
      } else {
        return 'not-in-first-screen'
      } 
    }
  }
}
</script>
