<template>
  <div class="nood-scroll-fade" ref = 'component1' 
    :style="scrollAni()">
    <slot></slot>
  </div>
</template>
<script>
export default {
    data() {
      return {
        end: 2,
        begin: 1,
        windosHeight: 0,
      }
    },
    props: {
      height: {
          default: 0
      }, 
      innerHeight: {
          default: 920
      },     
    },
    mounted() {
      this.windosHeight = document.documentElement.clientHeight
      this.end = this.$refs.component1.offsetTop
      this.begin = this.$refs.component1.offsetTop - this.windosHeight
    },
    methods: {
      scrollAni() {
        const scroll = this.$store.state.scroll
        const dur = scroll - this.begin
        let style = 'transform:translateY(0);'
        console.log(this.begin,this.end,scroll)
        if (scroll > this.begin && scroll < this.end){
          console.log('有效')
          style = 'transform:translateY('+ (-1) * dur * this.speed +'px);'
        }
        return style
      }
    }
}
</script>