              <h2 style="transition-delay:800ms;">百度智能云平台端设计 v3.0</h2>
            </div>
            <div class="detail">
              <div class="ani-wrp">
                <p style="transition-delay:900ms;">百度智能云在 2019 年位列全国云计算行业前三水准，需要一套新的品牌体系来支持不断壮大的业务，</p>
              </div>
              <div class="ani-wrp">
                <p style="transition-delay:950ms;">设计团队 CXD（cloud-experience-design）成员共同完成了新的品牌探索和设计工作。</p>
              </div>
              <div class="ani-wrp">
                <p style="transition-delay:1000ms;">我主要负责了其中UI方向的落地执行，目标是创建一套风格统一并与竞品有差异的 UI 系统。</p>
              </div>
              <div class="ani-wrp">
                <p style="transition-delay:1050ms;">同时，我也深入思考了业务需求，在视觉效果提升之外，让页面更好地支持业务。</p>
            </div>
            </div>
          </div>
          <div class="right">
            <div class="detail">
              <div class="ani-wrp">
                <p style="transition-delay:1200ms;">2019 中旬</p>
              </div>
              <div class="ani-wrp">
                <p style="transition-delay:1300ms;">Sketch, PS, AE, Vue.js</p>
              </div>
            </div>
          </div>
        </div>
      </ScrollClassAdd>
      <!--项目背景-->
      <ScrollClassAdd>
        <div class="work-bg">
          <div class="ani-wrp">
            <h3 style="transition-delay:0ms;">项目背景</h3>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:200ms;">1. 百度云更名为百度智能云，希望有一个新的品牌形象</p>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:300ms;">2. 国内公有云产品的网页设计风格同质化严重，缺乏辨识度</p>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:380ms;">3. 原有的视觉风格不能很好地承载云计算产品的多行业特性</p>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:420ms;">4. 设计团队也积累了一些原有网站的问题，希望通过改版来解决</p>
          </div>
          <div class="right">
            <div class="banner-content">
              <div class="button-content">
                <h4 :class="workBg == 0 ? 'active':'not-active'" @click="workBg =0">{{banner[0].title}}</h4>
                <h4 :class="workBg == 1 ? 'active':'not-active'" @click="workBg =1">{{banner[1].title}}</h4>
                <h4 :class="workBg == 2 ? 'active':'not-active'" @click="workBg =2">{{banner[2].title}}</h4>
              </div>
              <div class="banner-scroll">
                <img :src="banner[workBg].img" alt="">
              </div>
              <div class="right-arr"></div>
              <div class="left-arr"></div>
            </div>
          </div>
        </div>
      </ScrollClassAdd>
      <!--颜色风格-->
      <ScrollClassAdd>
        <div class="work-color">
