            <h3 style="transition-delay:200ms;">建立具有辨识度的颜色系统</h3>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:300ms;">颜色倾向是页面给人的最直观的印象，国内公有云的网站大多以暗色 banner 结合光感线条元素。</p>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:380ms;">这次我们大胆采用了以白色为主基调，蓝色为主题色，灰色为辅助色的配色方案，以获得最佳的品牌辨识度；</p>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:420ms;">同时，我们也思考如何用这些颜色承载我们的品牌理念。</p>
          </div>
          <div class="color-content">
            <div class="color-card color-1" style="transition-delay:600ms;">
