                <p class="white">蓝，是象征科技的理性之色</p>
                <p class="white">最受大众接受，有极强的包容性</p>
                <p class="bottom white">#2468F2</p>
              </div>
              <div class="color-mask" style="background-color:#2468f2;"></div>
              <div class="img-content">
                  <img src="@/assets/page/blue.jpg" alt="">
              </div>
            </div>
            <div class="color-card color-1" style="transition-delay:800ms; left:12px;">
              <div class="text-content">
                <h4 class="white">商务之灰</h4>
                <p class="white">灰，是商务人士的成熟稳重</p>
                <p class="white">体现出稳定和安全</p>
                <p class="bottom white">#373F5E</p>
              </div>
              <div class="color-mask" style="background-color:#373F5E;"></div>
              <div class="img-content">
                  <img src="@/assets/page/gray.jpg" alt="">
              </div>
            </div>
            <div class="color-card color-1" 
              style="transition:.6s transform 900ms, .6s opacity 900ms, .6s box-shadow; left:24px;">
              <div class="text-content">
                <h4>未来之白</h4>
                <p>白，象征未来的无限可能性</p>
                <p>是简单的哲学</p>
                <p class="bottom">#F6F8FB</p>
              </div>
              <div class="color-mask" style="background-color:#F6F8FB;"></div>
              <div class="img-content">
                  <img src="@/assets/page/white.jpg" alt="">
              </div>
            </div>
          </div>
        </div>
      </ScrollClassAdd>
      <!--元素风格-->
      <ScrollClassAdd>
        <div class="work-color work-yuan">
          <div class="circle-ani">
            <div class="circle-3 circle"></div>
            <div class="circle-2 circle"></div>
            <div class="circle-4 circle"></div>
            <div class="circle-1 circle"></div>
