          </div>
          <div class="ani-wrp">
            <p style="transition-delay:380ms;">科技是极客的，是酷的，是对世界的理性的分析和解读。</p>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:420ms;">但是现在，我们更关注科技与人的关系，科技应该更好地服务于人。</p>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:460ms;">从方到圆，从科技到科技与人的关系。</p>
          </div>
          <div class="right-card-content">
