            </div>
            <div class="ani-wrp">
              <p style="transition-delay:300ms;">文字是组成网页设计最核心的元素，</p>
            </div>
            <div class="ani-wrp">
              <p style="transition-delay:380ms;">我们对网站使用的文字进行了精心打磨和设计。</p>
            </div>
          </div>
          <ScrollClassAdd :beginPositon = "0.2">
