                  <div class="line"></div>
                  <p style="transition-delay:800ms;">对文字的大小设计，我们延续了以往的实践，</p>
                  <p style="transition-delay:900ms;">深入研究文字和行高的关系，寻找设计依据。</p>
                  <a href="https://v3.yunshe.design/lab/line_height.html" target="block" style="transition-delay:1000ms;"><p>了解详情</p></a>
                </div>
                <div class="img">
                  <img src="@/assets/page/text-1.png" alt="">
                </div>
                <div class="bg-mask"></div>
              </div>
              <div class="card-2 card">
