            </ScrollClassAdd>
            <ScrollClassAdd>
              <div class="resource-card">
                <img src="@/assets/page/icon80.png" alt="">
              </div>
            </ScrollClassAdd>
            <ScrollClassAdd>
              <div class="resource-card">
                <img src="@/assets/page/img-resource.png" alt="">
              </div>
            </ScrollClassAdd>
            <ScrollClassAdd>
              <div class="resource-card">
                <img src="@/assets/page/img-mian.png" alt="">
              </div>
            </ScrollClassAdd>
          </div>
        </div>
      </ScrollClassAdd>
      <!--动态图标-->
      <ScrollClassAdd>
