          <div class="ani-wrp">
            <p style="transition-delay:380ms;">经过大量的实践、竞品分析、思考、测试，我总结出了一套快速应对产品需求的方法论。</p>
          </div>
          <div class="ani-wrp">
            <p style="transition-delay:460ms;">按照这个思路，在 2019 年云智峰会前，一周的时间内支持了 13 个新产品的上线。</p>
          </div>
        </div>
        <div class="ani-under img-content">
          <img src="@/assets/page/jiagou.png" alt="">
        </div>
      </div>
      </ScrollClassAdd>
      <!--产品Banner设计-->
      <scrollClassAdd>
        <div class="work-color work-mfs work-banner-3">
          <div class="right">
            <div class="ani-wrp">
              <h3 style="transition-delay:200ms;">产品类型页面头图设计</h3>
            </div>
            <div class="ani-wrp">
              <p style="transition-delay:300ms;">产品类型页面分成三种。</p>
            </div>
            <div class="ani-wrp">
              <p style="transition-delay:380ms;">产品和专项类型解决方案在页面的内容上近似，面向用户不同，设计的时候使用了相同的排版和不同的 Banner 风格，以体现这个本质。</p>
            </div>
