<template>
  <div class="scroll-class-add" ref = 'component' 
    :class="scrollClass()">
    <slot></slot>
  </div>
</template>
<style lang="stylus">
.scroll-class-add
  &:after
    content ''
    display block
    clear both
</style>
<script>
export default {
    data() {
      return {
        toTop: 100000,
        height: 0
      }
    },
    props: {
      beginPosition : {
        default: 0.05
      },
      toTopParent : {
        default: 0
      }
    },
    mounted() {
      this.height = document.documentElement.clientHeight
      this.toTop = this.$refs.component.getBoundingClientRect().top + this.toTopParent
    },
    methods: {
      scrollClass() {
        const scroll = this.$store.state.scroll
        const start = this.toTop - this.height 
        if(scroll > (start + (this.beginPosition * this.height))){
          return 'in-window'
        } else {
          return 'not-in-window'
        }
      }
    }
}
</script>